export const UpdateMyChatList = (name, friendID, text, { sent, recieved }) => {
    // one time u load from server, then new messages are simply pushed to previous data
    return async(dispatch, getState) => {
        try {
            const previous = [...getState().chats];
            // is it ok to use by refrence ?
            // const friendIndex = previous.findIndex(chat => chat.with === friendID);
            previous.find(chat => chat.with === friendID).messages.push({
                name: name, //is name needed?
                me: sent ? text : null,
                friend: recieved ? text : null,
                date: new Date(),
            });

            await dispatch({
                type: "LOAD_CHATS",
                payload: previous,
            });

            // const friendList = [...getState().friends];
            // const friendIndex = friendList.findIndex(friend => friend.userID === friendID);
            // const recentInteractedFriend = {...friendList[friendIndex]};
            // delete friendList[friendIndex];
            // friendList.unshift(recentInteractedFriend);
            // await dispatch({
            // 	type: "LOAD_FRIENDS",
            // 	payload: friendList,
            // });

        } catch (err) {
            console.log(err);
            //..propeer message
            // ...reset?
        }
    };
};

